html {
    box-sizing: border-box;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
}

body {
    margin: 0;
    background-color: $white;
    font-family: 'PT Serif', serif;
    font-weight: 400;
}

img {
    max-width: 100%;
}

a {
    text-decoration: none;
    color: inherit;
    display: inline-block;
}

.list-reset {
    list-style: none;
    padding: 0;
    margin: 0;
}

.is-outer {
    position: relative !important; // stylelint-disable-line declaration-no-important
    left: -9999px !important; // stylelint-disable-line declaration-no-important
}

.site-container {
    overflow: hidden;
}

.is-hidden {
    display: none !important; // stylelint-disable-line declaration-no-important
}

.for-mobile {
    display: none;
}

.error {
    border: 2px #ff4019 solid !important; // stylelint-disable-line declaration-no-important
}

.js-focus-visible :focus:not(.focus-visible) {
    outline: none;
}

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;
    border: 0;
    clip: rect(0 0 0 0);
}

.btn-reset {
    border: none;
    background: transparent;
    cursor: pointer;
    padding: 0;
}

.btn {
    transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;
}

.container {
    margin: 0 auto;
    padding: 0 15px;
    max-width: 1200px;
}

.title {}



.mobile--overlay{
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(34,31,38,.8);
    z-index: 500;
    opacity: 0;
    visibility: hidden;
    transition: .3s ease-in-out;
}
.overlay--visible{
    opacity: 1;
    visibility: visible;
    transition: .3s ease-in-out;
}
.no-scroll{
    height: 100vh;
    overflow: hidden;
}